import React from 'react';
import Header from './Header';
import { useSelector } from 'react-redux';//Hook to access the global state
import currencyformatter from 'currency-formatter';
import { Link } from 'react-router-dom';


const Home = () => {
    const { products } = useSelector(state => state.ProductsReducer);
    console.log(products);
    return (
        <div>
            <Header />
            <div className="home_container">
                <div className="row">
                    {products.map(product => (
                        <div className="col-3" key={product.id}>
                            <div className="product">
                                <div className="product_img">
                                    <Link to="/details"></Link>
                                    <img src={product.img_url} alt="" />
                                </div>
                                <div className="product_name">
                                    {product.name}
                                </div>
                                <div className="product_description">
                                    {product.description}
                                </div>
                                <div className="row">
                                    <div className="col-6">
                                        <div className="product_price">

                                            {currencyformatter.format(product.final_price, { code: 'USD' })}

                                        </div>
                                    </div>
                                    <div className="col-6">
                                        <div className="original-price">
                                            <span className="actualprice">{currencyformatter.format(product.original_price, { code: 'USD' })}</span>

                                            
                                        </div>
                                    </div>
                                    <Link className="btn" to="/cart" >Add To Cart</Link>
                                </div>
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    )
}
export default Home;